import useHarvestFromBoardroom, { useEarningsOnBoardroom } from 'hooks/useHarvestFromBoardroom'
import React from 'react'
import styled from 'styled-components'
import { Button, Card } from 'uikit'
import Value from 'components/Value'
import Label from 'components/Label'
import { CardLogo } from 'views/Home/Home'

const Harvest: React.FC<any> = ({ canClaimReward }) => {
  const { onReward } = useHarvestFromBoardroom()
  const earnings = useEarningsOnBoardroom()

  return (
    <Card>
      <CardLogo token="sdo">
        <StyledCardContentInner>
          <StyledCardHeader>
            <Value value={earnings.toFixed(4)} />
            <Label text="SDO Earned" />
          </StyledCardHeader>
          <StyledCardActions>
            <Button onClick={onReward} disabled={!canClaimReward}>
              Claim Reward
            </Button>
          </StyledCardActions>
        </StyledCardContentInner>
      </CardLogo>
    </Card>
  )
}

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[6]}px;
  width: 100%;
`

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`

export default Harvest
