import React, { useCallback, useState } from 'react'
import { useApproveBoardroom } from 'hooks/useApprove'
import styled from 'styled-components'
import DepositModal from 'views/Farms/components/DepositModal'
import WithdrawModal from 'views/Farms/components/WithdrawModal'
import { AddIcon, Button, Card, IconButton, MinusIcon, useModal, Text } from 'uikit'
import Value from 'components/Value'
import Label from 'components/Label'
import { QuoteToken } from 'config/constants/types'
import useTokenBalance from 'hooks/useTokenBalance'
import { useStakeToBoardroom, useWithdrawFromBoardroom } from 'hooks/useStake'
import { CardLogo } from 'views/Home/Home'
import Countdown, { CountdownRenderProps } from 'react-countdown'

const IconButtonWrapper = styled.div`
  display: flex;
  svg {
    width: 20px;
  }
`

const Divider = styled.div`
  background-color: ${({ theme }) => theme.colors.borderColor};
  height: 1px;
  margin: 19px auto;
  width: 100%;
`
const launchTime = new Date(1618142400000)

const Stake: React.FC<any> = ({ canWithdraw, balanceOf, balanceOfRaw }) => {
  const [requestedApproval, setRequestedApproval] = useState(false)
  const { onApprove, isApproved } = useApproveBoardroom()
  const tokenBalance = useTokenBalance(QuoteToken.SSO)
  const { onStake } = useStakeToBoardroom()
  const { onWithdraw } = useWithdrawFromBoardroom()

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const [onPresentDeposit] = useModal(<DepositModal max={tokenBalance} onConfirm={onStake} tokenName="SSO" />)
  const [onPresentWithdraw] = useModal(<WithdrawModal max={balanceOfRaw} onConfirm={onWithdraw} tokenName="SSO" />)

  const isLaunched = Date.now() >= launchTime.getTime()

  const renderer = (countdownProps: CountdownRenderProps) => {
    const { days, hours, minutes, seconds } = countdownProps
    const paddedSeconds = seconds < 10 ? `0${seconds}` : seconds
    const paddedMinutes = minutes < 10 ? `0${minutes}` : minutes
    const paddedHours = hours < 10 ? `0${hours}` : hours
    const paddedDays = days > 0 ? `${days} days and ` : ''

    return (
      <>
        <Text pr="3px">
          Boardroom starts: {paddedDays}
          {paddedHours}:{paddedMinutes}:{paddedSeconds}
        </Text>
        <Divider />
      </>
    )
  }

  return (
    <Card>
      <CardLogo token="sso">
        <StyledCardContentInner>
          <StyledCardHeader>
            <Value value={balanceOf?.toFixed(4)} />
            <Label text="SSO Staked" />
          </StyledCardHeader>
          <StyledCardActions>
            {!isLaunched && <div><Countdown date={launchTime} renderer={renderer} /></div>}
            {!isApproved ? (
              <Button disabled={requestedApproval} onClick={handleApprove}>
                Approve SSO
              </Button>
            ) : (
              <IconButtonWrapper>
                <IconButton variant="tertiary" onClick={onPresentWithdraw} mr="6px" disabled={!canWithdraw}>
                  <MinusIcon color="primary" />
                </IconButton>
                <IconButton variant="tertiary" onClick={onPresentDeposit} disabled={!isLaunched}>
                  <AddIcon color="primary" />
                </IconButton>
              </IconButtonWrapper>
            )}
          </StyledCardActions>
        </StyledCardContentInner>
      </CardLogo>
    </Card>
  )
}

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[6]}px;
  width: 100%;
`

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`

export default Stake
