import { useCallback } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useDispatch } from 'react-redux'
import { Farm } from 'state/types'
import { stake, restake, stakeBoardroom, unstakeBoardroom } from 'utils/callHelpers'
import { useBoardroom, useMasterchef } from './useContract'
import { fetchFarmUserDataAsync } from '../state/farms'

export const useStake = ({ contractPid, type }: Farm) => {
  const dispatch = useDispatch()
  const { account } = useWallet()
  const masterChefContract = useMasterchef(type)

  const handleStake = useCallback(
    async (amount: string) => {
      await stake(masterChefContract, contractPid, amount, account)
      dispatch(fetchFarmUserDataAsync(account))
    },
    [account, dispatch, masterChefContract, contractPid],
  )

  return { onStake: handleStake }
}

export const useStakeToBoardroom = () => {
  const { account } = useWallet()
  const contract = useBoardroom()

  const handleStake = useCallback(
    async (amount: string) => {
      await stakeBoardroom(contract, amount, account)
    },
    [account, contract],
  )

  return { onStake: handleStake }
}

export const useWithdrawFromBoardroom = () => {
  const { account } = useWallet()
  const contract = useBoardroom()

  const handleStake = useCallback(
    async (amount: string) => {
      await unstakeBoardroom(contract, amount, account)
    },
    [account, contract],
  )

  return { onWithdraw: handleStake }
}

export const useReStake = ({ contractPid, type }: Farm) => {
  const dispatch = useDispatch()
  const { account } = useWallet()
  const masterChefContract = useMasterchef(type)

  const handleStake = useCallback(async () => {
    const txHash = await restake(masterChefContract, contractPid, account)
    dispatch(fetchFarmUserDataAsync(account))
    console.info(txHash)
  }, [account, dispatch, masterChefContract, contractPid])

  return { onStake: handleStake }
}

export default useStake
